html {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: -apple-system, Roboto, Segoe UI, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 1em;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

*, :before, :after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button, input, form {
  font: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden:not(:focus):not(:active), input[type="checkbox"].visually-hidden, input[type="radio"].visually-hidden {
  width: 1px;
  height: 1px;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  flex-direction: column;
  padding: 20px 15px;
  display: flex;
}

.form {
  margin: 20px auto;
  display: flex;
}

.page {
  max-width: 700px;
  min-width: 360px;
  color: #fff;
  background-color: #46afdf;
  border-radius: 5%;
  flex-direction: column;
  margin: 0 auto;
  display: flex;
}

.header {
  margin-bottom: 15px;
  padding: 10px;
}

.header__city {
