.container {
  display: flex;
  flex-direction: column;
  padding: 20px 15px;
}

.form {
  display: flex;
  margin: 20px auto;
}

.page {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 700px;
  min-width: 360px;

  border-radius: 5%;
  background-color: rgb(70, 175, 223);
  color: rgb(255, 255, 255);
}

.header {
  margin-bottom: 15px;
  padding: 10px;

  &__city {
    margin: 0;
    padding: 0;

    font-size: 26px;
    font-weight: 700;
    text-align: center;
  }

  &__date,
  &__timezone {
    margin: 0;
    padding: 0;

    color: rgb(10, 108, 164);
  }

  &__timezone {
    font-size: 12px;
  }
}

.main {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  padding: 0 20px;
}

.temperature {
  width: 40%;

  &__value {
    margin: 0;
    padding: 0;

    font-size: 80px;
    line-height: 60px;
    font-weight: 700;

    & sup {
      font-size: 50px;
      font-weight: 400;
    }
  }

  &__feelslike {
    margin: 0;
    padding: 0;

    font-size: 14px;
    font-weight: 800;
  }
}

.weather {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;

  &__icon {
    margin: 0;
    padding: 0;
    padding-top: 10px;
    padding-bottom: 3px;
  }

  &__descriptions {
    margin: 0;
    padding: 0;

    font-size: 14px;
    font-weight: 800;
    text-align: center;
  }
}

.feature {
  margin-bottom: 15px;

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;

    margin: 0;
    padding: 0;
  }

  &__item {
    margin: 0;
    padding: 0;
    padding-left: 15px;

    font-size: 14px;
  }

}